
import { ref, defineComponent } from "vue";

export default defineComponent({
  props: {
    text: { type: String, default: "" },
  },
  emits: ["confirm"],
  setup(props, { emit }) {
    const prompt = ref(false);
    function confirm() {
      prompt.value = false;
      emit("confirm");
    }
    return { prompt, confirm };
  },
});
